<!-- 用户登录界面 -->
<template>
  <div id="login">
    <div class="bg"></div>
    <el-row class="main-container">
      <el-col :lg="8" :md="10" :span="10" :xs="16">
        <div class="top">
          <i class="iconfont icon-kaoshi"></i
          ><span class="title">在线考试系统</span>
        </div>
        <div class="bottom">
          <div class="bg-color"></div>
          <div class="container">
            <p class="title">账号登录</p>
            <el-form
              ref="formLabelAlign"
              :label-position="labelPosition"
              :model="formLabelAlign"
              :rules="rules"
              label-width="80px"
            >
              <el-form-item label="账号" prop="username">
                <el-input
                  v-model="formLabelAlign.username"
                  placeholder="请输入账号"
                ></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input
                  v-model="formLabelAlign.password"
                  placeholder="请输入密码"
                  type="password"
                ></el-input>
              </el-form-item>
              <div class="submit">
                <el-button
                  class="row-login"
                  type="primary"
                  @click="login('formLabelAlign')"
                >登录
                </el-button
                >
              </div>
              <div class="options">
                <p class="find">
                  <a href="javascript:;" @click="changeType(2)">忘记密码？</a>
                </p>
                <div class="register">
                  <span>没有账号?</span>
                  <span
                  ><a href="javascript:;" @click="changeType(1)"
                  >点击注册</a
                  ></span
                  >
                </div>
              </div>
            </el-form>
          </div>
        </div>
        <el-dialog :visible.sync="flag" title="请选择账号类型" width="30%">
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="toStudent()">学生</el-button>
            <el-button type="primary" @click="toTeacher()">教师</el-button>
          </span>
        </el-dialog>
      </el-col>
    </el-row>
    <el-row class="footer">
      <el-col>
        <p class="msg2">版权所有 ©2023 聊城大学计算机学院软工4班张龙祥
          保留所有权利
          <a href="http://gat.shandong.gov.cn/" style="color:#ff962a "
             target="_blank">鲁ICP备19001371号</a> </p>
      </el-col>
    </el-row>
    <section class="remind">
      <span>学生账号：2019205008</span> <span>管理员账号：9527</span>
      <span>教师账号：20081003</span> <span>密码都是：123456</span>
    </section>
  </div>
</template>

<script>
import store from "@/vuex/store";
import {mapState} from "vuex";

export default {
  store,
  name: "login",
  data() {
    return {
      role: 2,
      type: 1,
      flag: false,
      labelPosition: "left",
      formLabelAlign: {
        username: "",
        password: ""
      },
      rules: {
        username: {
          required: true,
          message: "用户名不能为空",
          trigger: "blur"
        },
        password: {required: true, message: "密码不能为空", trigger: "blur"}
      }
    };
  },
  methods: {
    //用户登录请求后台处理
    login(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios({
            url: `/api/login`,
            method: "post",
            data: {
              ...this.formLabelAlign
            }
          }).then(res => {
            let resData = res.data.data;
            if (resData != null) {
              switch (resData.role) {
                case "0": //管理员
                  this.$cookies.set("tname", resData.adminName);
                  this.$cookies.set("tid", resData.adminId);
                  this.$cookies.set("role", 0);
                  this.$router.push({path: "/index"}); //跳转到首页
                  break;
                case "1": //教师
                  this.$cookies.set("tname", resData.teacherName);
                  this.$cookies.set("tid", resData.teacherId);
                  this.$cookies.set("role", 1);
                  this.$router.push({path: "/index"}); //跳转到教师用户
                  break;
                case "2": //学生
                  this.$cookies.set("cname", resData.studentName);
                  this.$cookies.set("cid", resData.studentId);
                  this.$router.push({path: "/student"});
                  break;
              }
            }
            if (resData == null) {
              //错误提示
              this.$message({
                showClose: true,
                type: "error",
                message: "用户名或密码错误"
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    clickTag(key) {
      this.role = key;
    },
    toStudent() {
      if (this.type == 1) {
        this.$router.push("sRegister");
      } else {
        this.$router.push("sForget");
      }
    },
    toTeacher() {
      if (this.type == 1) {
        this.$router.push("tRegister");
      } else {
        this.$router.push("tForget");
      }
    },
    changeType(num) {
      this.flag = true;
      this.type = num;
    }
  },
  computed: mapState(["userInfo"]),
  created() {
    this.formLabelAlign.username = this.$route.params.username;
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
.remind {
  border-radius: 4px;
  padding: 10px 20px;
  display: flex;
  position: fixed;
  right: 20px;
  bottom: 50%;
  flex-direction: column;
  color: #606266;
  background-color: #fff;
  border-left: 4px solid #409eff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.container {
  margin-bottom: 28px;
}

.container .el-radio-group {
  margin: 30px 0px;
}

a:link {
  color: #ff962a;
  text-decoration: none;
}

#login {
  font-size: 14px;
  color: #000;
  background-color: #fff;
}

#login .bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow-y: auto;
  height: 100%;
  background: url("../../assets/img/login-bg.jpg") center top / cover no-repeat;
  background-color: #b6bccdd1 !important;
}

#login .main-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

#login .main-container .top {
  margin-top: 80px;
  font-size: 30px;
  color: #ff962a;
  display: flex;
  justify-content: center;
}

#login .top .icon-kaoshi {
  font-size: 80px;
}

#login .top .title {
  margin-top: 20px;
}

#login .bottom {
  display: flex;
  justify-content: center;
  background-color: #fff;
  background-color: transparent !important;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
  position: relative;
}

.bg-color {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f1f1f1;
  opacity: 0.3;
  z-index: -1;
}

#login .bottom .title {
  text-align: center;
  font-size: 30px;
}

.bottom .container .title {
  margin: 30px 0px;
}

.bottom .submit .row-login {
  width: 100%;
  background-color: #04468b;
  border-color: #04468b;
  // margin: 20px 0px 10px 0px;
  padding: 15px 20px;
}

.bottom .submit {
  display: flex;
  justify-content: center;
}

.footer {
  margin-top: 50px;
  text-align: center;
}

.footer .msg1 {
  font-size: 18px;
  color: #fff;
  margin-bottom: 15px;
}

.footer .msg2 {
  font-size: 14px;
  color: #e3e3e3;
  margin-top: 90px;
}

.bottom .options {
  color: #ff962a;
  display: flex;
  justify-content: space-between;
  font-weight: 550;
  // font-size: 18px;
  margin-top: 12px;
}

.bottom .options > a {
  color: #ff962a;
}

.bottom .options .register span:nth-child(1) {
  color: #555555;
}

/deep/ .el-form--label-left .el-form-item__label {
  font-weight: 550;
  font-size: 18px;
  color: #000;
}

/deep/ .el-form-item__error {
  color: #ff962a;
  font-size: 12px;
  font-weight: 550;
}
</style>



